  background-size: 340px;
}
.post-office .walls .wall {
  position: absolute;
  border-style: solid;
  border-width: 1px 1px 1px 2px;
  border-color: #8c8c8c;
  border-left-color: #82626f;
  border-radius: 10px;
  background: url(/post-office-simulator/static/media/wall.498c93fa.jpg);
  background-size: 230px;
}
.post-office .walls .wall.bottom-left {
  left: -20px;
  width: 30px;
  top: 300px;
  bottom: -10px;
}
.post-office .walls .wall.top-left {
  left: -20px;
  width: 30px;
  top: -10px;
  height: 100px;
}
.post-office .walls .wall.bottom {
  left: 12px;
  right: 0;
  bottom: 0;
  height: 30px;
  border-width: 1px 0 0 0;
  border-radius: 0;
  background-position: 14px 0;
}
.post-office .walls .wall.top {
  left: 12px;
  right: 0;
  top: 0;
  height: 30px;
  border-width: 0 0 1px 0;
  border-radius: 0;
  background-position: 14px 0;
}
.post-office .walls .wall.top-right {
  right: 13px;
  width: 25px;
  top: 30px;
  bottom: 210px;
  border-width: 0 1px 1px 1px;
  border-radius: 0 0 10px 10px;
  border-color: #8c8c8c;
  background-position: 90px 64px;
}
.post-office .walls .wall.bottom-right {
  right: 13px;
  width: 25px;
  height: 60px;
  bottom: 30px;
  border-width: 1px 1px 0 1px;
  border-radius: 10px 10px 0 0;
  border-color: #8c8c8c;
  background-position: 50px 64px;
}
.post-office .walls .wall.shadow {
  box-shadow: rgba(0, 0, 0, 0.5) 0 0 30px 0;
}
.post-office .atm {
  position: absolute;
  bottom: 50px;
  left: 50px;
  width: 90px;
  height: 90px;
  background: url(/post-office-simulator/static/media/atm.394f141d.jpg) no-repeat center;
  background-size: contain;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.post-office .atm-shadow {
  position: absolute;
  bottom: 60px;
  left: 60px;
  width: 70px;
  height: 70px;
  box-shadow: black 0 5px 30px 0px;
}

.goal {
  position: absolute;
  left: 300px;
  top: 150px;
  width: 10px;
  height: 10px;
  border-radius: 100%;
