.Human {
  position: absolute;
  transition: all 300ms linear;
  top: -7px;
  left: -24px;
  width: 48px;
  height: 14px;
  background: none;
}

.body {
  z-index: 100;
  position: absolute;
  transition: all 300ms ease-in-out;
  width: 48px;
  height: 17.2px;
  border-radius: 5pt;
  background: #f0f0f0;
}

.chosen .body {
  background: #2734ab;
}

.worker .body {
  background: #de641d;
}

.body-shadow {
  z-index: 2;
  position: absolute;
  width: 48px;
  height: 17.2px;
  border-radius: 5pt;
  box-shadow: rgba(0, 0, 0, 0.5) 0 2px 40px 4px;
}

.balloon {
  transition: all 300ms ease-in-out;
  z-index: 102;
  position: absolute;
  top: 17.2px;
  left: 0;
  right: 0;
  margin: auto;
  width: 35px;
  height: 13.2px;
  border-radius: 3px;
  background: #f7f7f7;
}

.chosen .balloon {
  background: #1c278c;
}

.worker .balloon {
  background: #b3480b;
}

.head {
  z-index: 103;
  position: absolute;
  transition: all 200ms ease-in-out;
  top: -3.6px;
  left: 0;
  right: 0;
  width: 24px;
  height: 24px;
  margin: auto;
  border-radius: 50% 50%;
  background: #28184a;
}
.head .flare {
  transition: all 200ms ease-in-out;
  position: absolute;
  top: 10px;
  left: 10px;
  width: 1px;
  height: 4px;
  -webkit-transform: rotate(-95deg) translateY(9px) rotate(-95deg);
          transform: rotate(-95deg) translateY(9px) rotate(-95deg);
  border-radius: 100%;
  background: white;
}
.head .hat {
  z-index: 130;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 70%;
  border-radius: 100%;
  background: #fafafa;
}

.chosen .hat {
  background: #4450bd;
}

.foots {
  position: relative;
}
.foots .foot {
  position: absolute;
  transition: all 265ms ease-in-out;
  top: 0;
  width: 16.5px;
  height: 0;
  border-radius: 20pt;
  background: #ebebeb;
}
.foots .left.foot {
  left: 4px;
  top: 8.6px;
}
.foots .right.foot {
  right: 4px;
  top: 8.6px;
}

.chosen .foot {
  background: #1f2661;
}

.worker .foot {
  background: #c94e06;
}

.boats .boat {
  position: absolute;
  transition: all 300ms linear;
  top: -5px;
  width: 13.5px;
  height: 22px;
  border-radius: 50% 50% 40% 40%;
  background: #454545;
}
.boats .left.boat {
  left: 5.5px;
}
.boats .right.boat {
  right: 5.5px;
}

